body {
    background-color: rgb(223, 228, 240);
}

#header {
    display: flex;
    flex-direction: column;
    
    font-family: Arial, Helvetica, sans-serif; 
    font-weight: 400;
    background-color: beige; 
    color: rgb(122, 212, 122);
    margin-top: 50px; 
    font-size: 24px;
    
    border-radius: 20px;
    height: 1500px;
    box-shadow: grey;
    margin-bottom: 100px;
}

#header-container {
 display: flex;
 flex-direction: column;   
}

.main-header-title {
    display: flex;
    justify-content: center;
}

.header-title {
    
    border-radius: 30px;
    text-align: center;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 44px;
    width: 1050px;
    margin-top: 20px;
    margin-bottom: -90px;
    height: 100px;
    
}

.header-title h3 {
    margin-top: 19px;
    color: rgb(191, 8, 197);
}

.header-pic {
    display: flex;
    justify-content: center;
}

.main-app-title {
    margin-top: 20px;
    text-align: center;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 44px;
    color: black;
    border: 8px solid white;
    border-radius: 40px;
    height: 680px;
    width: 1450px;
    background-size: cover;
    background-position-y: -50px;
    display: flex;
    justify-content: center;
    filter: contrast(120%);
    background-image: url("./assets/AdobeStock_251323238.jpeg");
}

.main-app-title h3 {
    margin-top: 520px;
    color: rgb(27, 2, 73);
    font-size: 60px;
    border-bottom: 3px solid rgb(27, 2, 73);
}

.select-aircraft {
    display: flex;
    margin-top: 0px;
    margin-left: -50px;
    justify-content: space-around;
    width: 750px;
    font-family: Arial, Helvetica, sans-serif;
    border: 2px solid rgb(27, 2, 73);
    border-radius: 30px;
    padding: 20px;
    font-size: 18px;
    margin-bottom: 30px;
    background-color: rgb(2, 1, 59);
}

.select-aircraft-content {
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.optionClass {
    width: 150px;
    border: 1px solid rgb(103, 211, 103);
    border-radius: 30px;
    height: 60px; 
}

.select-aircraft-content h1 {
    font-size: 32px;
    text-transform: uppercase;
    margin-right: 25px;
    color: rgb(191, 8, 197);
}

.select-aircraft button {
    width: 140px;
    border: 1px solid rgb(27, 2, 73);
    margin-left: 20px;
    border-radius: 30px;
    background-color: rgb(191, 8, 197);
    color: rgb(27, 2, 73);
    height: 40px;
    font-size: 18px;
}

.selected-ordered-services {
    display: flex;
    font-family: Arial, Helvetica, sans-serif;
    margin-top: 100px;
}

.select-option {
    margin-left: 30px;
    margin-right: 30px;
}

.main-content {
    margin-left: -70px;
    margin-top: 100px;
    margin-bottom: 100px;
}

.aircraft-pic {
    width: 60px;
    height: 60px;
    background-size: cover;
    background-repeat: no-repeat;
    border: 1px solid rgb(27, 2, 73);
    border-radius: 80px;
    background-position-x: 0px;
    margin-left: 10px;
    margin-right: 10px;
    margin-top: 10px;
    background-image: url("./assets/pic7.jpeg");
}


.services-main {
    display: flex;
    align-items: center;
    padding: 0 30px;
}


.services-main p:last-child {
    margin-left: 10px;
    font-weight: 600;
}



.choosed-services {
    margin-left: 20px;
    font-family: Arial, Helvetica, sans-serif;
    border: 5px solid  rgb(27, 2, 73);
    padding: 20px;
    border-radius: 20px;
    background-color: rgba(131, 143, 247, 0.986);
}

.choosed-services h2 {
    text-align: center;
    font-family: Arial, Helvetica, sans-serif;
}

.choosed-services h4 {
    font-family: Arial, Helvetica, sans-serif;
    font-size: 24px;
    margin-left: 30px;
}

.choosed-services button {
    width: 140px;
    border: 1px solid black;
    margin-left: 50px;
    border-radius: 30px;
    background-color:  rgb(191, 8, 197);
    color: blanchedalmond;
    height: 40px;
    font-size: 18px;
}

.ordered-services {
    border: 5px solid  rgb(27, 2, 73);
    padding: 20px;
    margin-left: 20px;
    border-radius: 20px;
    width: 500px;
    text-align: center;
    font-family: Arial, Helvetica, sans-serif;
    background-color: rgba(131, 143, 247, 0.986);
}

.ordered-services h1 {
    font-family: Arial, Helvetica, sans-serif;
}

.ordered-services button {
    width: 140px;
    border: 1px solid black;
    margin-left: 20px;
    border-radius: 30px;
    background-color:  rgb(191, 8, 197);
    color: blanchedalmond;
    height: 40px;
    font-size: 18px;
}

.choosed-order-localS {
    border: 3px solid blanchedalmond;
    margin-bottom: 10px;
    padding: 20px;
    border-radius: 30px;
}

.main-content {
    display: flex;
}

#root {
    font-family: Arial, Helvetica, sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 50px;
}

#root button {
    width: 140px;
    border: 1px solid black;
    margin-left: 20px;
    border-radius: 30px;
    background-color:  rgb(191, 8, 197);
    color: blanchedalmond;
    height: 50px;
    font-size: 18px;
    outline: none;
}

#basket-button {
    margin-top: 10px;
    margin-bottom: 50px;
}

.basket-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 5px solid rgb(27, 2, 73);
    border-radius: 30px;
    width: 500px;
    height: 370px;
    background-color: rgba(131, 143, 247, 0.986);
}

.basket-content h1 {
    margin-top: 60px;
}